'use client';

import { 
  Search, 
  Bell, 
  Settings, 
  Sun, 
  Flag,
  MoreVertical,
  TrendingUp,
  Users,
  ShoppingCart,
  DollarSign,
  Workflow,
  Bot,
  BarChart3
} from 'lucide-react';

const statsCards = [
  {
    title: 'Total Workflows',
    value: '248',
    change: '+12%',
    changeType: 'positive',
    icon: Workflow,
    gradient: 'from-blue-500 to-blue-600',
    description: 'All created workflows'
  },
  {
    title: 'Active Workflows',
    value: '156',
    change: '+8%',
    changeType: 'positive',
    icon: Settings,
    gradient: 'from-green-500 to-green-600',
    description: 'Currently running'
  },
  {
    title: 'Total Agents',
    value: '47',
    change: '+5%',
    changeType: 'positive',
    icon: Bot,
    gradient: 'from-purple-500 to-purple-600',
    description: 'AI agents deployed'
  },
  {
    title: 'Success Rate',
    value: '94.3%',
    change: '+2.1%',
    changeType: 'positive',
    icon: BarChart3,
    gradient: 'from-orange-500 to-orange-600',
    description: 'Workflow completion rate'
  }
];

export default function DashboardPage() {
  return (
    <div className="bg-gray-900 min-h-screen text-white">
      {/* Header */}
      <header className="bg-gray-800 px-6 py-4 border-b border-gray-700">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button className="p-2 rounded-lg hover:bg-gray-700">
              <div className="w-6 h-6 grid grid-cols-3 gap-0.5">
                {[...Array(9)].map((_, i) => (
                  <div key={i} className="w-1 h-1 bg-gray-400 rounded-full"></div>
                ))}
              </div>
            </button>
            <h1 className="text-xl font-semibold">TimeLine</h1>
          </div>
          
          <div className="flex items-center space-x-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <input
                type="text"
                placeholder="Search something.."
                className="bg-gray-700 text-white pl-10 pr-4 py-2 rounded-lg w-64 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <button className="p-2 rounded-lg hover:bg-gray-700">
              <Flag className="w-5 h-5" />
            </button>
            <button className="p-2 rounded-lg hover:bg-gray-700">
              <Bell className="w-5 h-5" />
            </button>
            <button className="p-2 rounded-lg hover:bg-gray-700">
              <Settings className="w-5 h-5" />
            </button>
            <button className="p-2 rounded-lg hover:bg-gray-700">
              <Sun className="w-5 h-5" />
            </button>
            <div className="flex items-center space-x-2">
              <div className="w-8 h-8 bg-blue-500 rounded-full flex items-center justify-center">
                <span className="text-white font-medium text-sm">NH</span>
              </div>
              <span className="text-sm font-medium">Nowak Helme</span>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <div className="p-6">
        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {statsCards.map((card, index) => (
            <div key={index} className="bg-gray-800 rounded-2xl p-6 shadow-lg hover:shadow-xl transition-all duration-300 border border-gray-700 hover:border-gray-600">
              {/* Header */}
              <div className="flex items-center justify-between mb-4">
                <div className={`p-3 rounded-xl bg-gradient-to-br ${card.gradient} shadow-lg`}>
                  <card.icon className="w-6 h-6 text-white" />
                </div>
                <button className="text-gray-400 hover:text-gray-300 p-1 rounded-lg hover:bg-gray-700 transition-colors">
                  <MoreVertical className="w-4 h-4" />
                </button>
              </div>
              
              {/* Content */}
              <div className="space-y-3">
                <div>
                  <h3 className="text-gray-300 text-sm font-medium mb-1">{card.title}</h3>
                  <p className="text-gray-500 text-xs">{card.description}</p>
                </div>
                
                <div className="flex items-end justify-between">
                  <div>
                    <div className="text-3xl font-bold text-white mb-1">{card.value}</div>
                    <div className="flex items-center space-x-1">
                      <TrendingUp className="w-3 h-3 text-green-400" />
                      <span className="text-green-400 text-sm font-semibold">{card.change}</span>
                      <span className="text-gray-500 text-xs">vs last month</span>
                    </div>
                  </div>
                </div>
              </div>
              
              {/* Progress Bar for Success Rate */}
              {card.title === 'Success Rate' && (
                <div className="mt-4">
                  <div className="w-full bg-gray-700 rounded-full h-2">
                    <div 
                      className={`bg-gradient-to-r ${card.gradient} h-2 rounded-full transition-all duration-500`}
                      style={{width: card.value}}
                    ></div>
                  </div>
                </div>
              )}
              
              {/* Mini chart indicator for other cards */}
              {card.title !== 'Success Rate' && (
                <div className="mt-4 flex justify-end">
                  <div className="h-8 w-16 bg-gray-700 rounded-lg flex items-center justify-center">
                    <div className={`w-8 h-2 bg-gradient-to-r ${card.gradient} opacity-60 rounded-full`}></div>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>

        {/* Charts Section */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {/* Orders Statistics */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-lg font-semibold text-white">Orders Statistics</h3>
              <button className="bg-gray-700 text-white px-4 py-2 rounded-lg text-sm hover:bg-gray-600 transition-colors">
                Refresh â†»
              </button>
            </div>
            
            <div className="flex items-center justify-center h-64">
              <div className="relative">
                <div className="w-48 h-48 rounded-full border-8 border-gray-700 flex items-center justify-center">
                  <div className="absolute inset-0 rounded-full">
                    <div className="absolute inset-0 rounded-full bg-blue-500 opacity-75" 
                         style={{clipPath: 'polygon(50% 50%, 50% 0%, 100% 0%, 100% 40%, 50% 50%)'}}></div>
                    <div className="absolute inset-0 rounded-full bg-cyan-400 opacity-75" 
                         style={{clipPath: 'polygon(50% 50%, 100% 40%, 100% 100%, 20% 100%, 50% 50%)'}}></div>
                    <div className="absolute inset-0 rounded-full bg-green-500 opacity-75" 
                         style={{clipPath: 'polygon(50% 50%, 20% 100%, 0% 100%, 0% 60%, 50% 50%)'}}></div>
                    <div className="absolute inset-0 rounded-full bg-pink-500 opacity-75" 
                         style={{clipPath: 'polygon(50% 50%, 0% 60%, 0% 0%, 50% 0%, 50% 50%)'}}></div>
                  </div>
                </div>
              </div>
            </div>
            
            <div className="grid grid-cols-2 gap-4 mt-6">
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
                <span className="text-gray-300 text-sm">Direct</span>
                <span className="text-white font-semibold">48.5%</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-pink-500 rounded-full"></div>
                <span className="text-gray-300 text-sm">Social</span>
                <span className="text-white font-semibold">10.4%</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-cyan-400 rounded-full"></div>
                <span className="text-gray-300 text-sm">Marketing</span>
                <span className="text-white font-semibold">33.6%</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <span className="text-gray-300 text-sm">Marketing</span>
                <span className="text-white font-semibold">7.5%</span>
              </div>
            </div>
          </div>

          {/* Statistics Chart */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-lg font-semibold text-white">Statistics</h3>
              <button className="text-gray-400 hover:text-white">
                <MoreVertical className="w-4 h-4" />
              </button>
            </div>
            
            <div className="h-64 flex items-end space-x-2">
              {[20, 40, 60, 80, 100, 75, 45, 65, 85, 50, 30].map((height, index) => (
                <div key={index} className="flex-1 bg-blue-500 rounded-t" 
                     style={{height: `${height}%`}}></div>
              ))}
            </div>
            
            <div className="mt-4 flex items-center justify-between text-sm text-gray-400">
              <span>2019</span>
              <span>2025</span>
            </div>
          </div>
        </div>

        {/* Revenue Chart */}
        <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
          <div className="flex items-center justify-between mb-6">
            <h3 className="text-lg font-semibold text-white">Total Revenue</h3>
            <button className="text-gray-400 hover:text-white">
              <MoreVertical className="w-4 h-4" />
            </button>
          </div>
          
          <div className="h-64 relative">
            <div className="absolute inset-0 flex items-end">
              <svg className="w-full h-full" viewBox="0 0 400 200">
                <polyline
                  fill="none"
                  stroke="#10b981"
                  strokeWidth="2"
                  points="0,150 50,120 100,140 150,100 200,110 250,80 300,90 350,70 400,60"
                />
                <polyline
                  fill="none"
                  stroke="#3b82f6"
                  strokeWidth="2"
                  points="0,180 50,170 100,160 150,150 200,140 250,130 300,125 350,120 400,115"
                />
              </svg>
            </div>
          </div>
          
          <div className="flex items-center justify-between mt-4">
            <div className="flex items-center space-x-6">
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <span className="text-gray-300 text-sm">Total Income</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
                <span className="text-gray-300 text-sm">Total Expenses</span>
              </div>
            </div>
            <div className="text-gray-400 text-sm">
              Jan Feb Mar Apr May Jun Jul Aug Sep Oct Nov Dec
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}